
.text01{
  color:#00f;
  font-size: 30px;
  font-weight: bold;
}
.logo_img{
  width: 100%;
  margin: 0 auto;
  text-align: center;

}
.logo_img img{
  width: 200px;
}
.img_size{
  justify-content: center;
  display: flex;
  margin: 0 auto;
  width: 100%;

}
.img_size IMG{
  margin: 0px 10px;
}
.h2_txt{
  width: 100%;
  margin: 0 auto;
  text-align: center;
}
.a_link{
  width: 100%;
  margin: 0 auto;
  text-align: center;
}
.a_link a{
  text-decoration: none;
  color:#333;
  display: inline-block;
  padding: 10px;
  background: #f0f0f0;
  border-radius: 20px;
  margin: 10px;
}
.a_link a:hover{
  background:purple;
  color:#fff;
}
.kurly_box{
  text-align: center;
  padding: 0;
  margin: 0 auto;
  width: 1200px;
}
.kurly_product{
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  list-style: none;
}
.kurly_product li{
  margin: 0 10px;
}
.kurly_product img{
  width: 100%;
}
.kurly_product p{
  line-height: 10px;
}
.sale{
  color:rgb(250, 98, 47);;
  font-weight: bold;
}
.kullyloginform{
  margin: 0 auto;
  width: 400px;
}
.kullyloginform fieldset {
  border: none;
}
.kullyloginform fieldset legend{
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin: 30px 0px;
}
.kullyloginform label{
  display: none;
}
.kullyloginform input{
  display: block;
  width: 100%;
  height: 50px;
  margin-bottom: 10px;
}
.kullyloginform p{
  text-align: right;
  font-size: 16px;
}
.kullyloginform p a{
  text-decoration: none;
  color: #333;
  font-size: 14px;
}
.kullyloginform input[type=submit]{
  background: rgb(95, 0, 128); 
  border: none;
  color: #fff;
}
.kullyloginform input[type=button]{
  background: #fff;
  border: 1px solid rgb(95, 0, 128);
}